<template>
  <div id="app">
    <link rel="stylesheet" href="/static/foundation.min.css">
    <table class="row">
      <tr>
      <td width="50%">
        <task-list :tasks="tasks"></task-list>
      </td>
      <td width="50%">
        <tender :tasks="tasks"></tender>
      </td>
      </tr>
    </table>

  </div>
</template>


<script>
import TaskList from './TaskList.vue'
import Tender from './Tender.vue'

export default {
  name: 'app',
  components: { TaskList, Tender },
  data: () => ({
    tasks: [{
      title: `1. Credit Guarantee Fund Trust for Micro and Small Enterprises (hereinafter
referred to as Trust) is a trust formed by Govt. of India and SIDBI for providing
guarantee to the credit facilities extended by banks and financial institutions to Micro
and Small Enterprises. The Trust is operating from its Mumbai Office and Chennai
cell. `,
      completed: false
    }]
  })
}
</script>

<style>
body {
  background-color: #abc;
}

*,
h1,
button {
  font-family: 'Nunito', sans-serif;
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.tasks {
  width: 90%;
  max-width: 45rem;
  padding: 1em;
  margin: 1em auto;
  overflow: auto;
  background-color: #fff;
  box-shadow: 0px 0.25rem 1rem rgba(0, 0, 0, 0.25);
}

td{
    vertical-align: top;
}


.tasks__list {
  clear: both;
}

.tasks__item {
  margin-bottom: 0.5em;
  position: relative;
}

.tasks__item__toggle {
  cursor: pointer;
  width: 100%;
  text-align: left;
  padding: 0.85em 2.25em 0.85em 1em;
  background-color: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.tasks__item__toggle:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border-color: rgba(0, 0, 0, 0.15);
}

.tasks__item__toggle--completed {
  text-decoration: line-through;
  background-color: rgba(0, 128, 0, 0.15);
  border-color: rgba(0, 128, 0, 0.2);
}

.tasks__item__toggle--completed:hover {
  background-color: rgba(0, 128, 0, 0.25);
  border-color: rgba(0, 128, 0, 0.3);
}

.tasks__item__remove {
  position: absolute;
  height: 100%;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.tasks__item__remove i {
  vertical-align: middle;
}
</style>



